import React from "react";
import Table from "../Table/Table";
import "./Weather.css";

class Weather extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            brand: "sexyDance",
        };
    }
    render() {
        return (
            <div className="container">
                <table className="table">
                    <h2>{this.props.day} </h2>
                    <Table celcius={25} day="Monday" />
                    <Table celcius={26} day="Tuesday" />
                    <Table celcius={25} day="Wednesday" />
                    <Table celcius={25} day="Thursday" />
                    <Table celcius={25} day="Friday" />
                    <Table celcius={25} day="Saturday" />
                    <Table celcius={25} day="Sunday" />
                </table>
            </div>
        );
    }
}

export default Weather;
